	module.exports = {"data":{"site":{"siteMetadata":{"startDate":"2018-06-06","lessons":8,"noClass":[4]}},"markdownRemark":{"html":"<h2>History of Flex Box</h2>\n<p>The Flex box Layout (Flexible Box) module (currently a W3C Last Call Working Draft) aims at providing a more efficient way to lay out, align and distribute space among items in a container, even when their size is unknown and/or dynamic (thus the word \"flex\").</p>\n<p>The main idea behind the flex layout is to give the container the ability to alter its items' width/height (and order) to best fill the available space (mostly to accommodate to all kind of display devices and screen sizes). A flex container expands items to fill available free space, or shrinks them to prevent overflow.</p>\n<p>Most importantly, the flex box layout is direction-agnostic as opposed to the regular layouts (block which is vertically-based and inline which is horizontally-based). While those work well for pages, they lack flexibility (no pun intended) to support large or complex applications (especially when it comes to orientation changing, resizing, stretching, shrinking, etc.).</p>\n<p><strong>Note:</strong> Flex box layout is most appropriate to the components of an application, and small-scale layouts, while the Grid layout is intended for larger scale layouts.</p>\n<h2>Properties for the Parent (flex container)</h2>\n<h3>display</h3>\n<p>This defines a flex container; inline or block depending on the given value. It enables a flex context for all its direct children.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container {\n  display: flex; /* or inline-flex */\n}</code></pre>\n      </div>\n<p>Note that CSS columns have no effect on a flex container.</p>\n<iframe height='400' scrolling='no' title='Flex Intro - flex' src='//codepen.io/danhahn/embed/VvzQwP/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/VvzQwP/'>Flex Intro - flex</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>flex-direction</h3>\n<p>This establishes the main-axis, thus defining the direction flex items are placed in the flex container. Flex box is (aside from optional wrapping) a single-direction layout concept. Think of flex items as primarily laying out either in horizontal rows or vertical columns.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container {\n  flex-direction: row | row-reverse | column | column-reverse;\n}</code></pre>\n      </div>\n<ul>\n<li>row (default): left to right in ltr; right to left in rtl</li>\n<li>row-reverse: right to left in ltr; left to right in rtl</li>\n<li>column: same as row but top to bottom</li>\n<li>column-reverse: same as row-reverse but bottom to top</li>\n</ul>\n<iframe height='400' scrolling='no' title='Flex Intro- flex-direction' src='//codepen.io/danhahn/embed/rOzJNX/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/rOzJNX/'>Flex Intro- flex-direction</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>flex-wrap</h3>\n<p>By default, flex items will all try to fit onto one line. You can change that and allow the items to wrap as needed with this property. Direction also plays a role here, determining the direction new lines are stacked in.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container{\n  flex-wrap: nowrap | wrap | wrap-reverse;\n}</code></pre>\n      </div>\n<ul>\n<li>nowrap (default): single-line / left to right in ltr; right to left in rtl</li>\n<li>wrap: multi-line / left to right in ltr; right to left in rtl</li>\n<li>wrap-reverse: multi-line / right to left in ltr; left to right in rtl</li>\n<li>flex-flow (Applies to: parent flex container element)</li>\n<li>This is a shorthand flex-direction and flex-wrap properties, which together define the flex container's main and cross axes. Default is row nowrap.</li>\n</ul>\n<iframe height='400' scrolling='no' title='Flex Intro - flex-wrap' src='//codepen.io/danhahn/embed/YyxePZ/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/YyxePZ/'>Flex Intro - flex-wrap</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">flex-flow: &lt;‘flex-direction’&gt; || &lt;‘flex-wrap’&gt;</code></pre>\n      </div>\n<h3>justify-content</h3>\n<p>This defines the alignment along the main axis. It helps distribute extra free space left over when either all the flex items on a line are inflexible, or are flexible but have reached their maximum size. It also exerts some control over the alignment of items when they overflow the line.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container {\n  justify-content: flex-start | flex-end | center | space-between | space-around;\n}</code></pre>\n      </div>\n<ul>\n<li>flex-start (default): items are packed toward the start line</li>\n<li>flex-end: items are packed toward to end line</li>\n<li>center: items are centered along the line</li>\n<li>space-between: items are evenly distributed in the line; first item is on the start line, last item on the end line</li>\n<li>space-around: items are evenly distributed in the line with equal space around them. Note that visually the spaces aren't equal, since all the items have equal space on both sides. The first item will have one unit of space against the container edge, but two units of space between the next item because that next item has its own spacing that applies.</li>\n</ul>\n<iframe height='400' scrolling='no' title='Flex Intro - justify-content' src='//codepen.io/danhahn/embed/vNJdOZ/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/vNJdOZ/'>Flex Intro - justify-content</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>align-items</h3>\n<p>This defines the default behaviour for how flex items are laid out along the cross axis on the current line. Think of it as the justify-content version for the cross-axis (perpendicular to the main-axis).</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container {\n  align-items: flex-start | flex-end | center | baseline | stretch;\n}</code></pre>\n      </div>\n<ul>\n<li>flex-start: cross-start margin edge of the items is placed on the cross-start line</li>\n<li>flex-end: cross-end margin edge of the items is placed on the cross-end line</li>\n<li>center: items are centered in the cross-axis</li>\n<li>baseline: items are aligned such as their baselines align</li>\n<li>stretch (default): stretch to fill the container (still respect min-width/max-width)</li>\n</ul>\n<iframe height='400' scrolling='no' title='Flex Intro - align-items' src='//codepen.io/danhahn/embed/MavQwd/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/MavQwd/'>Flex Intro - align-items</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>align-content</h3>\n<p>This aligns a flex container's lines within when there is extra space in the cross-axis, similar to how justify-content aligns individual items within the main-axis.</p>\n<p><strong>Note:</strong> this property has no effect when there is only one line of flex items.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.container {\n  align-content: flex-start | flex-end | center | space-between | space-around | stretch;\n}</code></pre>\n      </div>\n<ul>\n<li>flex-start: lines packed to the start of the container</li>\n<li>flex-end: lines packed to the end of the container</li>\n<li>center: lines packed to the center of the container</li>\n<li>space-between: lines evenly distributed; the first line is at the start of the container while the last one is at the end</li>\n<li>space-around: lines evenly distributed with equal space around each line</li>\n<li>stretch (default): lines stretch to take up the remaining space</li>\n</ul>\n<h2>Properties for the Children (flex items)</h2>\n<h3>order</h3>\n<p>By default, flex items are laid out in the source order. However, the order property controls the order in which they appear in the flex container.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  order: &lt;integer&gt;;\n}</code></pre>\n      </div>\n<h3>flex-grow</h3>\n<p>This defines the ability for a flex item to grow if necessary. It accepts a unitless value that serves as a proportion. It dictates what amount of the available space inside the flex container the item should take up.</p>\n<p>If all items have flex-grow set to 1, the remaining space in the container will be distributed equally to all children. If one of the children a value of 2, the remaining space would take up twice as much space as the others (or it will try to, at least).</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  flex-grow: &lt;number&gt;; /* default 0 */\n}</code></pre>\n      </div>\n<p>Negative numbers are invalid.</p>\n<iframe height='400' scrolling='no' title='Flex Intro - flex-grow' src='//codepen.io/danhahn/embed/gaxvge/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/gaxvge/'>Flex Intro - flex-grow</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>flex-shrink</h3>\n<p>This defines the ability for a flex item to shrink if necessary.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  flex-shrink: &lt;number&gt;; /* default 1 */\n}</code></pre>\n      </div>\n<p>Negative numbers are invalid.</p>\n<h3>flex-basis</h3>\n<p>This defines the default size of an element before the remaining space is distributed. It can be a length (e.g. 20%, 5rem, etc.) or a keyword. The auto keyword means \"look at my width or height property\" (which was temporarily done by the main-size keyword until deprecated). The content keyword means \"size it based on the item's content\" - this keyword isn't well supported yet, so it's hard to test and harder to know what its brethren max-content, min-content, and fit-content do.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  flex-basis: &lt;length&gt; | auto; /* default auto */\n}</code></pre>\n      </div>\n<p>If set to 0, the extra space around content isn't factored in. If set to auto, the extra space is distributed based on its flex-grow value. See this graphic.</p>\n<iframe height='400' scrolling='no' title='Flex Intro - Flex Basis' src='//codepen.io/danhahn/embed/NGvydE/?height=300&theme-id=16874&default-tab=result&embed-version=2' frameborder='no' allowtransparency='true' allowfullscreen='true' style='width: 100%;'>See the Pen <a href='http://codepen.io/danhahn/pen/NGvydE/'>Flex Intro - Flex Basis</a> by Dan Hahn (<a href='http://codepen.io/danhahn'>@danhahn</a>) on <a href='http://codepen.io'>CodePen</a>.\n</iframe>\n<h3>flex</h3>\n<p>This is the shorthand for flex-grow, flex-shrink and flex-basis combined. The second and third parameters (flex-shrink and flex-basis) are optional. Default is 0 1 auto.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  flex: none | [ &lt;&#39;flex-grow&#39;&gt; &lt;&#39;flex-shrink&#39;&gt;? || &lt;&#39;flex-basis&#39;&gt; ]\n}</code></pre>\n      </div>\n<p>It is recommended that you use this shorthand property rather than set the individual properties. The short hand sets the other values intelligently.</p>\n<h3>align-self</h3>\n<p>This allows the default alignment (or the one specified by align-items) to be overridden for individual flex items.</p>\n<p>Please see the align-items explanation to understand the available values.</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">.item {\n  align-self: auto | flex-start | flex-end | center | baseline | stretch;\n}</code></pre>\n      </div>\n<p>Note that float, clear and vertical-align have no effect on a flex item.</p>","frontmatter":{"lesson":"Intro to Flex box","title":"Lesson 6","lessonId":6,"downloads":null}},"allMarkdownRemark":{"edges":[{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/6/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 6","lesson":"Float Layout"},"fields":{"slug":"/6/index.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/6/clear-fix.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 6","lesson":"Clear fix"},"fields":{"slug":"/6/clear-fix.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/6/flex.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 6","lesson":"Intro to Flex box"},"fields":{"slug":"/6/flex.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/6/flex-layout.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 6","lesson":"Flex Layout"},"fields":{"slug":"/6/flex-layout.html"}}},{"node":{"id":"/Users/dhahn/dev/html-class-g/src/pages/6/homework.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Lesson 6","lesson":"Homework"},"fields":{"slug":"/6/homework.html"}}}]}},"pathContext":{"slug":"/6/flex.html","title":"Lesson 6"}}

